<template>
     <el-form :model="searchForm" ref="searchForm"  label-width="80px" >
            <el-row>
                <el-col :span="6">
                  <el-form-item label="商户" prop="rolesEnable">
                    <el-select v-model="searchForm.rolesEnable">
                      <el-option class="radio" value="1" label="慕斯"></el-option>
                      <el-option class="radio" value="0" label="菜鸟物流"></el-option>
                      <el-option class="radio" value="0" label="北美之家"></el-option>
                      <el-option class="radio" value="0" label="箭牌"></el-option>
                    </el-select>
                  </el-form-item>
                </el-col>
                <el-col :span="6" class="flex-right">
                    <el-button type="primary" @click.native="search">查询</el-button>
                    <el-button @click="resetForm('searchForm')">重置</el-button>
                </el-col>
            </el-row>
          </el-form>
</template>

<script>
/**
 * Roles Search Component
 * ----------------------
 * Author: zhouqi
 * Date: 2018.03.20
 */
export default {
  name: 'DevelopSearch',
  data () {
    return {
      searchForm: {
        rolesName: '',
        rolesEnable: '',
        rolesRemark: ''
      }
    }
  },
  methods: {
    // 检索
    search: function () {
      alert(111)
      this.$emit('search', Object.assign({}, this.searchForm))
    },
    // 重置
    resetForm (formName) {
      this.$refs[formName].resetFields()
      this.$emit('rest', Object.assign({}, this.searchForm))
    }
  }

}
</script>
<style lang="scss" scoped>
.el-form-item{
   margin-bottom:10px;
}
</style>



